import React from 'react'
import {BrowserRouter as Router, Route} from 'react-router-dom'
import BindPhone from '../pages/bindPhone/BindPhone'
import Approval from '../pages/approval/Approval'

export class Routes extends React.Component {
    render() {
        return <Router basename='/wxService'>
            <div style={{width: '100%', height: '100%'}}>
                <Route path='/bindPhone' component={BindPhone}/>
                <Route path='/bindPhone/:openId' component={BindPhone}/>
                <Route path='/approval' component={Approval}/>
                <Route path='/approval/:mainId/:master:/phone' component={Approval}/>
            </div>
        </Router>
    }
}


