import React, {Component} from 'react'
import './tableItem.css'
import {Input, Modal} from 'semantic-ui-react'
import {DatePicker} from 'antd'
import locale from 'antd/lib/date-picker/locale/zh_CN'

export default class TableItem extends Component {

    constructor() {
        super()

        this.state = {
            modalOpen: false,
            selectList: ['选项一', '选项二', '选项三', '选项四', '选项五'],
            inputValue: ''
        }
    }

    render() {
        let valueItem = '';
        const dateFormat = 'YYYY-MM-DD';
        const {inputValue} = this.state
        switch (this.props.type) {
            case '1':
                //文本
                valueItem = <div>PR189236234</div>
                break
            case '2':
                //输入框
                valueItem = <Input className='value-input' placeholder='请输入'
                                   value={inputValue} onChange={this.inputChange}/>
                break
            case '3':
                //日期选择框
                valueItem = <DatePicker locale={locale} className='value-input'
                                        defaultValue={inputValue}
                                        format={dateFormat} size='small'
                                        onChange={this.onDatePicker}/>
                break
            case '4':
                //弹框选择
                const {modalOpen, selectList} = this.state
                let modalItems = []
                for (let i = 0; i < selectList.length; i++) {
                    modalItems.push(<div className='selectItem' onClick={
                        this.modalSelect.bind(this, i)
                    }>{selectList[i]}</div>)
                }
                valueItem = <Modal trigger={<Input className='value-input'
                                                   readOnly unselectable='on' placeholder='请选择'
                                                   onClick={this.onSelectClick}
                                                   value={inputValue}/>}
                                   open={modalOpen}
                                   onClose={this.modalClose}
                                   size='small'>
                    <Modal.Content image>
                        <Modal.Description>
                            {modalItems}
                        </Modal.Description>
                    </Modal.Content>
                </Modal>

                break
            case '5':
                //dbfind
                valueItem = <Input className='value-input'
                                   readOnly unselectable='on' placeholder='请选择'
                                   onClick={this.onDbfindClick}
                                   value={inputValue}/>
                break
            default:
                valueItem = <div></div>
                break
        }

        return <div className='table-parent'>
            <div className='table-caption'>请购单号</div>
            <div className='table-value'>
                {valueItem}
            </div>
        </div>
    }

    modalClose = () => {
        this.setState({
            modalOpen: false
        })
    }

    modalSelect = (index) => {
        const {selectList} = this.state
        this.setState({
            inputValue: selectList[index],
            modalOpen: false
        })
    }

    inputChange = (e) => {
        this.setState({
            inputValue: e.target.value
        })
    }

    onDatePicker = (e, dateString) => {
        this.setState({
            inputValue: dateString
        })
    }

    onSelectClick = () => {
        this.setState({
            modalOpen: true
        })
    }

    onDbfindClick = () => {
        console.log('dbfind跳页面选择')
    }
}