import React, {Component} from 'react'
import './approvalNode.css'
import {Icon} from 'semantic-ui-react'
import {Avatar} from 'antd'


export default class ApprovalNode extends Component {

    render() {
        return <div className='parent'>
            <div className='dateLayout'>
                <div className='dateItem'>10-17</div>
                <div className='dateItem'>11:02</div>
            </div>
            <div className='arrowLayout'>
                <div className='arrowLine'></div>
                <Icon color='grey' name='chevron circle up' style={{margin: '0px', padding: '0px'}}/>
                <div className='arrowLine'></div>
            </div>
            <Avatar shape="square" src={require('@/images/default_header.png')} className='headerImg'/>
            <div className='contentLayout'>
                <div className='nameItem'>饶猛</div>
                <div className='opinionItem'>审批意见</div>
            </div>
            <div className='stateItem'>等待审批</div>
        </div>
    }
}