import React, {Component} from 'react'
import {Image, Input, Button} from 'semantic-ui-react'
import './bindPhone.css'
// import {BrowserRouter as Router, Link} from 'react-router-dom'
// import {bindActionCreators} from 'redux'
// import {connect} from 'react-redux'
// import * as userActions from '@/redux/actions/user'
// import Approval from '../approval/Approval'
import {message} from 'antd'
import 'whatwg-fetch'

export default class BindPhone extends Component {

    constructor(props) {
        super(props);

        this.state = {
            phone: ''
        }
    }

    componentDidMount() {
        // this.props.userActions.login()
        console.log(this.props.match.params.openId)
    }

    render() {
        return <div className='bindParent'>
            <Image className='uasIcon' src={require('@/images/ic_uas_icon.png')}/>

            <div className='phoneLayout'>
                <div className='phoneCaption'>手机号</div>
                <input type='number' onKeyPress={this.phoneKeyPress} className='phoneInput' placeholder='请输入手机号'
                       value={this.state.phone} onChange={this.phoneChange}/>
            </div>

            <div onClick={this.bindPhoneEvent} className='bindButton'>
                {/*<Link className='bindButton' to='/approval'>*/}
                绑定
                {/*</Link>*/}
            </div>
        </div>
    }

    phoneKeyPress = (event) => {
        const invalidChars = ['-', '+', 'e', '.', 'E']
        if (invalidChars.indexOf(event.key) !== -1) {
            event.preventDefault()
        }
    }

    phoneChange = (e) => {
        console.log(e.target.value)
        this.setState({
            phone: e.target.value
        })
    }

    bindPhoneEvent = () => {
        let phoneRegex = /^((\+)?86|((\+)?86)?)0?1[3458]\d{9}$/;
        const {phone} = this.state

        if (phoneRegex.test(phone)) {
            // this.props.history.push('/approval')
            fetch('https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxbc1f8607137d3b8a&redirect_uri=http%3a%2f%2fqq784602719.imwork.net%2fwxService%2fwxlogin&response_type=code&scope=snsapi_userinfo&state='
                + phone
                + '#wechat_redirect', {
                method: 'GET',
                mode: 'no-cors'
            }).then((response) => {
                message.success(response, 0)
                // console.log(response)
            }).catch((error) => {
                message.success(error.toString(), 0)
            })
        } else {
            message.error('请输入正确的手机号码')
        }
    }
}