import React, {Component} from 'react'
import './approval.css'
import '../../index.css'
import {TextArea, Icon} from 'semantic-ui-react'
import PointsItem from '@/components/pointsItem/PointsItem'
import TableItem from '@/components/tableItem/TableItem'
import ApprovalNode from '@/components/approvalNode/ApprovalNode'
import {Popover, Spin, Avatar} from 'antd'
import ApprovalBean from '@/model/ApprovalBean'
import ApprovalRecordBean from '@/model/ApprovalRecordBean'
import 'whatwg-fetch'
import {fetchRequest} from '@/utils/fetchRequest'


let approvalRecord = new ApprovalRecordBean()
let titleApproval = new ApprovalBean();

let hineApprovals = [];//隐藏字段
let showApprovals = [];//显示字段
let historyNodes = [];//历史审批
let mainDetailList = [];//主从表
let setuptasList = [];//历史审批要点
let enclosureList = [];//附件
let nodeList = [];//审批节点
let pointsList = [];//要点


export default class Approval extends Component {

    constructor() {
        super()

        this.state = {
            approvalContent: '',
            fastList: [
                '赞',
                'OK',
                '加油',
                '好的',
                '请及时完成'
            ],
            fastModalOpen: false,
            approvalIndex: 0,
            loading: true,
            disagreeAble: true,//不同意按钮是否显示
            agreeAble: true,//同意按钮是否显示
            takeoverAble: false,//接管按钮是否显示
            optionAble: true,//底部审批操作布局是否显示
        }
    }

    componentWillMount() {
        //初始化时，获取审批单据数据
        fetchRequest('https://218.17.158.219:9443/uas/common/getCurrentNode.action',
            'POST', {
                'jp_nodeId': 59180421,
                'master': '',
                '_noc': 1
            })
            .then((response) => {
                console.log('getCurrentNode', response)

                let result = response.json();
                let infoObject = result.info

                let approvalBean = new ApprovalBean()
                approvalRecord.processInstanceId = infoObject.InstanceId
                approvalRecord.isForknode = (infoObject.forknode === 0)
                let currentnode = infoObject.currentnode
                if (currentnode != null && currentnode != undefined) {
                    let recordName = currentnode.jp_launcherName
                    let jp_nodeDealMan = currentnode.jp_nodeDealMan
                    approvalRecord.currentNodeMan = jp_nodeDealMan
                    if (jp_nodeDealMan == null || jp_nodeDealMan == undefined) {
                        this.setState({
                            disagreeAble: false,
                            agreeAble: false,
                            takeoverAble: true
                        })
                    } else {
                        this.setState({
                            agreeAble: true,
                            takeoverAble: false
                        })
                    }
                    let launcherCode = currentnode.jp_launcherId
                    let nodeName = currentnode.jp_nodeName
                    let keyValue = currentnode.jp_keyValue
                    let jp_name = currentnode.jp_name
                    approvalRecord.title = jp_name
                    approvalRecord.callerName = jp_name
                    approvalRecord.status = currentnode.jp_status
                    let caller = currentnode.jp_caller

                    if (keyValue != null && keyValue != undefined) {
                        approvalRecord.id = keyValue
                    }

                    if (approvalRecord.title != null && approvalRecord.title != undefined) {
                        if (recordName != null && recordName != undefined) {
                            approvalRecord.title = recordName + "  " + approvalRecord.title
                        }
                    }

                    if (caller != null && caller != undefined) {
                        approvalRecord.caller = caller
                    }

                    if (nodeName != null && nodeName != undefined) {
                        approvalRecord.nodeName = nodeName
                    }

                    if (approvalRecord.imid == null || approvalRecord.imid == undefined) {
                        approvalRecord.imid = ''
                    }
                }

                let button = result.button
                if (currentnode != null && currentnode != undefined) {
                    approvalRecord.needInputKeys = button.jt_neccessaryfield
                }

            }).catch((error) => {
            console.log('getCurrentNodeE', error)
        })
    }

    componentDidMount() {
        setInterval(() => {
            this.setState({
                loading: false
            })
        }, 1500)
    }

    render() {
        const {
            approvalContent,
            fastList,
            fastModalOpen,
            approvalIndex,
            disagreeAble,//不同意按钮是否显示
            agreeAble,//同意按钮是否显示
            takeoverAble,//接管按钮是否显示
            optionAble,
        } = this.state

        //审批常用语
        let fastItems = []
        for (let i = 0; i < fastList.length; i++) {
            fastItems.push(<div className='fastItem' onClick={
                this.fastSelect.bind(this, i)
            }>{fastList[i]}</div>)
        }


        let tableItems = []
        //主表字段
        for (let i = 1; i < 6; i++) {
            tableItems.push(<TableItem type={i + ""}></TableItem>)
        }

        //从表字段
        tableItems.push(<div className='gray-line' style={{height: '8px'}}></div>)
        tableItems.push(<div className='tableTitle'>从表明细</div>)
        for (let i = 1; i < 6; i++) {
            tableItems.push(<TableItem type={i + ""}></TableItem>)
        }

        //审批要点
        let pointItems = []
        pointItems.push(<div className='tableTitle'>审批要点</div>)
        for (let i = 1; i < 5; i++) {
            pointItems.push(<PointsItem type={i + ""}></PointsItem>)
        }

        //审批节点
        let approvalPoint = []

        approvalPoint.push(<div className='approval_tab_parent'>
            <div className={approvalIndex == 0 ?
                'approval_tab_selected' : 'approval_tab_normal'}
                 onClick={this.approvalTabSelect1}>审批节点
            </div>
            <div style={{background: '#cccccc', width: '1px', height: '20px'}}></div>
            <div className={approvalIndex == 1 ?
                'approval_tab_selected' : 'approval_tab_normal'}
                 onClick={this.approvalTabSelect2}>审批历史
            </div>
        </div>)

        for (let i = 0; i < 4; i++) {
            approvalPoint.push(<ApprovalNode></ApprovalNode>)
        }


        return <div style={{width: '100%', height: '100%'}}>
            <Spin size="large" style={{display: this.state.loading ? 'flex' : 'none'}}
                  tip='数据加载中...'>
            </Spin>

            <div className='root' style={{display: this.state.loading ? 'none' : 'flex'}}>
                < div className='content'>
                    <div className='headerLayout'>
                        <Avatar size={42} src={require('@/images/default_header.png')}/>
                        <div className='headerText'>饶猛的请假申请流程</div>
                    </div>
                    <div className='gray-line' style={{height: '1px'}}></div>
                    {tableItems}
                    <div className='gray-line' style={{height: '8px'}}></div>
                    {pointItems}
                    <div className='gray-line' style={{height: '8px'}}></div>
                    {approvalPoint}
                </div>

                <div className={optionAble ? 'bottomMenu' : 'displayNone'}>
                    <div className='gray-line' style={{height: '8px'}}></div>
                    <div className='editLayout'>
                        <Icon disabled name='edit'/>
                        <TextArea className='editInput'
                                  value={approvalContent}
                                  placeholder='请输入审批意见...'
                                  onChange={this.approvalEdit}/>
                        {/*<Modal trigger={<div className='fastWords' onClick={this.fastClick}>常</div>}
                                   open={fastModalOpen}
                                   onClose={this.fastModalClose}
                                   size='small'>
                                <Modal.Content image>
                                    <Modal.Description>
                                        {fastItems}
                                    </Modal.Description>
                                </Modal.Content>
                            </Modal>*/}
                        <Popover title='常用语' trigger='click'
                                 content={fastItems}
                                 visible={fastModalOpen}
                                 onVisibleChange={this.handleFastVisiable}>
                            <div className='fastWords' onClick={this.fastClick}>常</div>
                        </Popover>
                    </div>
                    <div className='gray-line' style={{height: '8px'}}></div>
                    <div className='menuParent'>
                        <div className={takeoverAble ? 'menuItem' : 'displayNone'} onClick={this.approvalTakeover}>接管
                        </div>
                        <div className={takeoverAble ? '' : 'displayNone'}
                             style={{background: '#cccccc', width: '1px', height: '24px'}}></div>

                        <div className={agreeAble ? 'menuItem' : 'displayNone'} onClick={this.approvalAgree}>同意</div>
                        <div className={agreeAble ? '' : 'displayNone'}
                             style={{background: '#cccccc', width: '1px', height: '24px'}}></div>

                        <div className={disagreeAble ? 'menuItem' : 'displayNone'} onClick={this.approvalDisagree}>不同意
                        </div>
                    </div>
                </div>
            </div>
        </div>
    }

    handerTitle = (reId) => {
        let approval = new ApprovalBean()
        if (approvalRecord.title != null && approvalRecord.title != undefined) {
            approval.caption = approvalRecord.title
        }
        if (approvalRecord.imid != null && approvalRecord.imid != undefined) {
            approval.idKey = approvalRecord.imid
        }
        if (reId > 0) {
            approval.id = reId
        }
        titleApproval = approval

    }

    handleFastVisiable = (visible) => {
        this.setState({
            fastModalOpen: visible
        })
    }

    approvalTabSelect1 = () => {
        this.setState({
            approvalIndex: 0
        })
    }

    approvalTabSelect2 = () => {
        this.setState({
            approvalIndex: 1
        })
    }

    //接管单据
    approvalTakeover = () => {
        console.log('接管')
    }

    //同意审批
    approvalAgree = () => {
        console.log('同意')
    }

    //不同意审批
    approvalDisagree = () => {
        console.log('不同意')
    }

    approvalEdit = (e) => {
        console.log(e.target.value)
        this.setState({
            approvalContent: e.target.value
        })
    }

    fastSelect = (index) => {
        const {approvalContent, fastList} = this.state
        this.setState({
            approvalContent: approvalContent + fastList[index],
            fastModalOpen: false
        })
    }

    fastClick = () => {
        this.setState({
            fastModalOpen: true
        })
    }

    fastModalClose = () => {
        this.setState({
            fastModalOpen: false
        })
    }
}
